  --white: #fff;
  --black: #141414;
  --green-base: #35ff90;
  --green-medium: #00f56e;
  --green-dark: #00e968;
  --green-light: #6bffb0;
  --gray-dark: #8e8e8e;
  --gray-light: #eaeaea;
  --font-lit: 1rem;
  --font-default: 1.125rem;
  --font-medium: 1.75rem;
  --font-medium-xl: 2.5rem;
  --font-big: 4.375rem;
}

