}
.bid__link {
  color: var(--black);
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  transition: 0.3s;
}
@media (any-hover: hover) {
  .bid__link:hover:hover {
    color: var(--green-medium);
  }
}

.footer {
  padding: 50px 0;
}
.footer__grid {
  display: grid;
  grid-template-columns: 460px 460px 421px;
  gap: 20px;
  justify-content: space-between;
}
.footer__item {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  font-size: 20px;
}
.footer__link {
  color: var(--black);
}